import React from 'react'
import {FaTwitter} from 'react-icons/fa';
import {FaLinkedin} from 'react-icons/fa';

import {
    Card,
    CardTitle,
    CardBody,
    CardText
} from 'reactstrap'
import {graphql, StaticQuery, Link} from 'gatsby'
import Img from 'gatsby-image'
import {createLink} from "../util/utilityFunctions";


const styles = {
    img: {
        width: '80px',
        alignSelf: 'center',
        margin: '1rem'
    }
};

const Sidebar = ({author, authorFluid}) => (
    <div>
        {author && (
            <Card>
                <Img className="card-image-top" fluid={authorFluid} style={styles.img}/>
                <CardBody>
                    <CardTitle className="text-center text-uppercase mb-3">
                        {author.name}
                    </CardTitle>
                    <CardText>{author.bio}</CardText>
                    <div className="author-social-links text-center">
                        <ul>
                            <li>
                                <a
                                    href={author.twitter}
                                    targe="_blank"
                                    rel="noopener noreferrer"
                                    className="twitter"
                                >
                                    <FaTwitter/>
                                </a>
                            </li>
                            <li>
                                <a
                                    href={author.linkedin}
                                    targe="_blank"
                                    rel="noopener noreferrer"
                                    className="linkedin"
                                >
                                    <FaLinkedin/>
                                </a>
                            </li>
                        </ul>
                    </div>
                </CardBody>
            </Card>
        )}

        {/*
    <Card>
      <CardBody>
        <CardTitle className="text-center text-uppercase">
          Advertisement
        </CardTitle>
        <img
          src="https://via.placeholder.com/320x200"
          alt="Advert"
          style={{ width: '100%' }}
        />
      </CardBody>
    </Card>
    */}

        <Card>
            <CardBody>
                <CardTitle className="text-center text-uppercase mb-3">
                    Recent Posts
                </CardTitle>
                <StaticQuery
                    query={sidebarQuery}
                    render={data => (
                        <div>
                            {data.allMarkdownRemark.edges.map(({node}) => (
                                <Card key={node.id}>
                                    <Link to={createLink(node)}>
                                        <Img
                                            className="card-image-top"
                                            fluid={node.frontmatter.image.childImageSharp.fluid}
                                        />
                                    </Link>
                                    <CardBody>
                                        <CardTitle>
                                            <Link to={createLink(node)}>
                                                {node.frontmatter.title}
                                            </Link>
                                        </CardTitle>
                                    </CardBody>
                                </Card>
                            ))}
                        </div>
                    )}
                />
            </CardBody>
        </Card>
    </div>
);

const sidebarQuery = graphql`
  query sidebarQuery {
    allMarkdownRemark(
      sort: { fields: [frontmatter___date], order: DESC }
      limit: 3
    ) {
      edges {
        node {
          id
          frontmatter {
            title
            category
            image {
              childImageSharp {
                fluid(maxWidth: 300) {
                  ...GatsbyImageSharpFluid
                }
              }
            }
          }
          fields {
            slug
          }
        }
      }
    }
  }
`;

export default Sidebar
